import axios from 'axios';

const serverUrl = process.env.REACT_APP_SERVER_URL;
const baseUrl = `${serverUrl}/api/mazes`;


export const saveMaze = async (usernameId, grid, accessToken,
    algorithm) => {
  const mazeObject = {
    usernameId: usernameId,
    mazeBoard: grid,
    typeOfAlgorithm: algorithm,
  };

  const config = {
    headers: {Authorization: `bearer ${accessToken}`},
  };

  const response = await axios.post(baseUrl, mazeObject, config);
  return response.data;
};

export const getMazesForUser = async (id, acessToken) => {
  const config = {
    headers: {Authorization: `bearer ${acessToken}`},
  };
  const response = await axios.get(`${baseUrl}/${id}`, config);
  return response.data;
};
