import React, {useState, useEffect} from 'react';
import {useAuth0} from '@auth0/auth0-react';
import {getMazesForUser} from '../services/mazeService';

import {TableContainer, Table, TableHead, TableRow,
  TableCell, TableBody, Paper} from '@material-ui/core';

const Mazes = () => {
  const {user, isAuthenticated, isLoading, getAccessTokenSilently} = useAuth0();
  const [mazes, setMazes] = useState([]);

  useEffect( async () => {
    const acessToken = await getAccessTokenSilently();
    const response = await getMazesForUser(user.sub, acessToken);
    setMazes(response);
  }, []);

  if (isLoading) {
    return (
      <div>Laoding..</div>
    );
  }

  return isAuthenticated && (
    <TableContainer component={Paper}>
      <Table sx={{minWidth: 650}} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>UsernameId</TableCell>
            <TableCell align="right">Date&nbsp;(g)</TableCell>
            <TableCell align="right">Type of algorithm&nbsp;(g)</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {mazes.map((maze) => (
            <TableRow
              key={maze.id}
              sx={{'&:last-child td, &:last-child th': {border: 0}}}
            >
              <TableCell component="th" scope="row">
                {maze.name}
              </TableCell>
              <TableCell align="right">{maze.usernameId}</TableCell>
              <TableCell align="right">{maze.date}</TableCell>
              <TableCell align="right">{maze.typeOfAlgorithm}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>

  );
};

export default Mazes;
